.about-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: .2rem .4rem;
    gap: .2rem .4rem;
}

.content_box *{
    list-style: none;
}

.content_box ul li::before{
    content: "-";
    padding-right: .4rem;
    text-indent: -5px;
}

