.content_box {
    background-color: #fff;
    background-image:
        linear-gradient(90deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
        linear-gradient(#eee .1em, transparent .1em);
    background-size: 90% .98em;
    background-position: -.5rem;
    box-shadow: var(--light-shadow) 2px 2px 2px 4px;
    margin: .5rem .5rem;
}

/* Animations */
@-webkit-keyframes about-list {
    0%   {opacity: 0;}
    15%  {opacity: .1;}
    25%  {opacity: .2;}
    45%  {opacity: .5;}
    70%  {opacity: .75;}
    100% {opacity: 1;}
}
@keyframes about-list {
    0%   {opacity: 0;}
    15%  {opacity: .1;}
    25%  {opacity: .2;}
    45%  {opacity: .5;}
    70%  {opacity: .75;}
    100% {opacity: 1;}
}
